.App {
  text-align: center;
}
.TopBar {
  position: absolute;
  top: 0;
  font-weight: bold;
  display: inline;
}
.TopBar ul li {
  float: right;
}
.content {
  
  color: white;
  left: 50%;
  align-items: center;
  justify-content: center;
  align-self: center;
}
.header {
  background-color: #041929;
  background: url("./imgs/header2.png");
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.footer {
  background-color: #041929;
  background: url("./imgs/footer.png");
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.display-text {
  opacity: 1;
  color: white;
}
.skills {
  height: 100px;
  top: 50%;
}
.title {
  font-weight: bold;
  font-size: 36px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.subtitle {
  font-weight: bold;
  font-size: 24px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.divide {
  margin-top: 100px;
  margin-bottom: 100px;
}
.email {
  font-weight: bold;
  color: #487ba1;
  text-decoration: none;
  transition: font-size 1s;
}
.email:hover {
  font-size: 20px;
  transition: font-size 1s;
}